<template>
  <div>
    <DevicesTable
      ref="devicesTable"
      @handle-edit="(id) => $refs.sidebarDevice.edit(id)"
    >
      <template v-slot:plus>
        <b-button
          variant="primary"
          @click="$refs.sidebarDevice.open()"
        >
          <span class="text-nowrap">Registrar Nuevo</span>
        </b-button>
      </template>
    </DevicesTable>
    <SidebarDevice
      ref="sidebarDevice"
      @reload="$refs.devicesTable.fetchList()"
    />
  </div>
</template>

<script>
import { ref } from '@vue/composition-api'
import { BButton } from 'bootstrap-vue'
import DevicesTable from './components/DevicesTable.vue'
import SidebarDevice from './components/SidebarDevice.vue'

export default {
  components: {
    BButton,
    DevicesTable,
    SidebarDevice,
  },
  setup() {
    const sidebarDevice = ref()
    const devicesTable = ref()
    return {
      devicesTable,
      sidebarDevice,
    }
  },
}
</script>

