import { useToast } from 'vue-toastification/composition'
import ToastificationContent from '@/@core/components/toastification/ToastificationContent.vue'
import DeviceService from '@/services/DeviceService'
import { reactive, ref } from '@vue/composition-api'

const useDevice = () => {
  const toast = useToast()
  const isSaving = ref(false)
  const isLoading = ref(false)
  const blank = {
    id: '',
    name: '',
    typeDevice: '',
    unit: '',
    status: true,
  }
  const states = reactive({
    name: {
      isInvalid: false,
      message: '',
    },
  })
  const isSidebarActive = ref(false)

  const device = reactive(blank)

  const resetData = () => {
    device.id = ''
    device.name = ''
    device.status = true
    device.typeDevice = ''
    device.unit = ''

    states.name.isInvalid = false
    states.name.message = ''
  }

  const saveNew = async () => {
    isSaving.value = true
    try {
      const { id, ...rest } = device
      await DeviceService.create(rest)
      isSaving.value = false
      isSidebarActive.value = false
      setTimeout(() => {
        resetData()
      }, 1000)
      toast({
        component: ToastificationContent,
        props: {
          title: 'Registrado correctamente!!',
          icon: 'CheckCircleIcon',
          variant: 'success',
        },
      })
      return true
    } catch (error) {
      isSaving.value = false
      if (error.response.data.statusCode === 400) {
        states.name.isInvalid = true
        states.name.message = error.response.data.message
      }
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al Guardar',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
      throw error
    }
  }

  const edit = async id => {
    isLoading.value = true
    try {
      const response = await DeviceService.getById(id)
      const {
        name,
        status,
        typeDevice,
        unit,
      } = response.data

      device.id = response.data.id
      device.name = name
      device.status = status
      device.typeDevice = typeDevice.id
      device.unit = unit?.id ?? ''

      isLoading.value = false
      isSidebarActive.value = true
    } catch (error) {
      isLoading.value = false
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al Consultar registro',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
      throw error
    }
  }

  const update = async () => {
    isSaving.value = true
    try {
      const { id, ...rest } = device
      await DeviceService.update(device.id, rest)
      isSaving.value = false
      isSidebarActive.value = false
      resetData()
      toast({
        component: ToastificationContent,
        props: {
          title: 'Actualizado Correctamente!',
          icon: 'CheckCircleIcon',
          variant: 'success',
        },
      })
    } catch (error) {
      isSaving.value = false
      if (error.response.data.statusCode === 400) {
        states.name.isInvalid = true
        states.name.message = error.response.data.message
      }
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al Actualizar',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
      throw error
    }
  }

  const save = async () => {
    if (device.id !== '') {
      await update()
    } else {
      await saveNew()
    }
  }

  return {
    isSaving,
    isSidebarActive,
    states,
    device,
    // methods
    save,
    resetData,
    edit,
    update,
  }
}
export default useDevice
