import { useToast } from 'vue-toastification/composition'
import ToastificationContentVue from '@/@core/components/toastification/ToastificationContent.vue'
import { ref, reactive, watch } from '@vue/composition-api'
import _ from 'lodash'
import DeviceService from '@/services/DeviceService'

const useDevicesTable = (perPage = 10) => {
  const title = 'Dispositivos'
  const toast = useToast()
  const isLoading = ref(false)
  const devices = ref([])

  const perPageOptions = [10, 25, 50]

  const headerColumns = [
    { key: 'id', label: '#', sortable: true },
    { key: 'name', label: 'Dispositivo', sortable: true },
    { key: 'typeDevice.name', label: 'Tipo de Dispositivo', sortable: true },
    {
      key: 'createdAt',
      label: 'Fecha de creacin',
      sortable: true,
      stickyColumn: true,
      isRowHeader: true,
      variant: 'success',
    },
    {
      key: 'updatedAt',
      label: 'Fecha de modificacin',
      sortable: true,
      stickyColumn: true,
      isRowHeader: true,
      variant: 'primary',
    },
    { key: 'status', label: 'Estatus', sortable: true },
    { key: 'actions', label: 'Acciones' },
  ]

  const listQuery = reactive({
    take: perPage,
    page: 1,
    order: 'ASC',
    pageCount: 1,
    itemCount: 0,
    search: '',
    sortBy: 'id',
    from: 0,
    to: 0,
  })

  const fetchList = async () => {
    isLoading.value = true
    try {
      const response = await DeviceService.fetchList(listQuery)
      const { data, meta } = response.data
      const {
        take,
        page,
        pageCount,
        itemCount,
        from,
        to,
      } = meta
      listQuery.itemCount = Number(itemCount)
      listQuery.pageCount = Number(pageCount)
      listQuery.take = Number(take)
      listQuery.from = Number(from)
      listQuery.to = to > data.length ? itemCount : to
      listQuery.page = page
      devices.value = data
      isLoading.value = false
    } catch (error) {
      isLoading.value = false
      toast({
        component: ToastificationContentVue,
        props: {
          title: `Error al cargar ${title}`,
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
    }
  }

  const resolveStatus = status => (status ? 'success' : 'danger')

  const reload = _.debounce(() => {
    fetchList()
  }, 500)

  watch(() => listQuery.take, () => fetchList())

  watch(() => listQuery.page, () => fetchList())

  watch(() => listQuery.search, () => {
    reload()
  })

  fetchList()

  return {
    title,
    perPageOptions,
    devices,
    headerColumns,
    isLoading,
    listQuery,
    resolveStatus,
    fetchList,
  }
}
export default useDevicesTable
