<template>
  <b-card
    no-body
    class="mb-0"
  >
    <b-overlay
      :show="isLoading"
      spinner-variant="primary"
      spinner-type="grow"
      spinner-small
      rounded="sm"
    >
      <div class="m-2">
        <!-- Table Top -->
        <b-row>
          <!-- Per Page -->
          <b-col
            cols="12"
            md="6"
            class="d-flex align-items-center justify-content-start mb-1 mb-md-0"
          >
            <label>Mostrar</label>
            <v-select
              v-model="listQuery.take"
              :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
              :options="perPageOptions"
              :clearable="false"
              class="per-page-selector d-inline-block mx-50"
            />
            <label>{{ title }}</label>
          </b-col>

          <!-- Search -->
          <b-col
            cols="12"
            md="6"
          >
            <div class="d-flex align-items-center justify-content-end">
              <b-form-input
                v-model="listQuery.search"
                class="d-inline-block mr-1"
                placeholder="Buscar..."
              />
              <slot name="plus">
                &nbsp;
              </slot>
            </div>
          </b-col>
        </b-row>
      </div>

      <b-table
        ref="refUsuariosListTable"
        class="position-relative"
        :items="devices"
        responsive
        :fields="headerColumns"
        primary-key="id"
        show-empty
        empty-text="No hay registros"
      >
        <!-- Column: Id -->
        <template #cell(id)="data">
          <span class="text-nowrap">
            {{ data.index + 1 }}
          </span>
        </template>
        <template #cell(name)="data">
          <b-media vertical-align="center">
            <template #aside>
              <b-link
                class="font-weight-bold d-block text-nowrap"
                @click="$emit('handle-edit', data.item.id)"
              >
                {{ data.item.name }}
              </b-link>
            </template>

            <!-- <small class="text-muted">@{{ data.item.username }}</small> -->
          </b-media>
        </template>

        <!-- Column: createdAt -->
        <template #cell(createdAt)="data">
          <span class="content-header-title">{{ dateTimeFormat(data.item.createdAt) }}</span>
        </template>

        <!-- Column: updatedAt -->
        <template #cell(updatedAt)="data">
          <span class="content-header-title">{{ dateTimeFormat(data.item.updatedAt) }}</span>
        </template>

        <!-- Column: status -->
        <template #cell(status)="data">
          <template>
            <b-badge
              pill
              :variant="`light-${resolveStatus(data.item.status)}`"
            >
              {{ data.item.status ? "Activo" : "Inactivo" }}
            </b-badge>
          </template>
        </template>

        <!-- Column: Actions -->
        <template #cell(actions)="data">
          <div class="text-nowrap">
            <feather-icon
              size="20"
              class="cursor-pointer"
              icon="EditIcon"
              @click="$emit('handle-edit', data.item.id)"
            />
          </div>
        </template>
      </b-table>
      <div class="mx-2 mb-2">
        <b-row>
          <b-col
            cols="12"
            sm="6"
            class="d-flex align-items-center justify-content-center justify-content-sm-start"
          >
            <span class="text-muted">
              Mostrando {{ listQuery.from }} a {{ listQuery.to }} de
              {{ listQuery.itemCount }} {{ title }}
            </span>
          </b-col>
          <!-- Pagination -->
          <b-col
            cols="12"
            sm="6"
            class="d-flex align-items-center justify-content-center justify-content-sm-end"
          >
            <b-pagination
              v-model="listQuery.page"
              :total-rows="listQuery.itemCount"
              :per-page="listQuery.take"
              first-number
              last-number
              class="mb-0 mt-1 mt-sm-0"
              prev-class="prev-item"
              next-class="next-item"
            >
              <template #prev-text>
                <feather-icon
                  icon="ChevronLeftIcon"
                  size="18"
                />
              </template>
              <template #next-text>
                <feather-icon
                  icon="ChevronRightIcon"
                  size="18"
                />
              </template>
            </b-pagination>
          </b-col>
        </b-row>
      </div>
    </b-overlay>
  </b-card>
</template>

<script>
import {
  BCard,
  BRow,
  BCol,
  BFormInput,
  BTable,
  BMedia,
  BLink,
  BBadge,
  BPagination,
  BOverlay,
} from 'bootstrap-vue'
import vSelect from 'vue-select'
import useCustomFunctions from '@/composables/useCustomFunctions'
import useDevicesTable from '../composables/useDevicesTable'

export default {
  components: {
    BCard,
    BRow,
    BCol,
    BFormInput,
    BTable,
    BMedia,
    BLink,
    BBadge,
    BPagination,
    BOverlay,
    vSelect,
  },
  setup() {
    const { dateTimeFormat } = useCustomFunctions()

    const {
      title,
      isLoading,
      perPageOptions,
      headerColumns,
      listQuery,
      devices,
      resolveStatus,
      fetchList,
    } = useDevicesTable(10)

    const statusOptions = [
      { label: 'Activo', value: 'activo' },
      { label: 'Inactivo', value: 'inactivo' },
    ]

    return {
      title,
      isLoading,
      perPageOptions,
      headerColumns,
      listQuery,
      devices,
      // Extra Filters

      // UI
      resolveStatus,
      statusOptions,
      fetchList,

      dateTimeFormat,
    }
  },
}
</script>

<style lang="scss" scoped>
.per-page-selector {
  width: 90px;
}
</style>

<style lang="scss">
@import "@core/scss/vue/libs/vue-select.scss";
</style>
